import React, { Component } from 'react';

import {
    Button,
    Container,
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem } from 'reactstrap';
import { Link } from 'react-router-dom';

import './header.css';

class Header extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.handleScroll = this.handleScroll.bind(this);
        this.state = {
            isOpen: false,
            scrolled: window.scrollY > 10
        };

    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll() {
        this.setState({
            scrolled: window.scrollY > 10
        });
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    render() {
        return (
            <header>
                <Navbar fixed="top" expand="md" light={true} color='light'>
                    <Container fluid>
                        <NavbarBrand href="/">
                            <span className="icon-logo-juz-entertainment"/>
                        </NavbarBrand>
                        <Button className="round-button round-button-red ml-auto mr-4 my-3">РУС</Button>
                        <NavbarToggler onClick={this.toggle}>
                            <span className="navbar-toggler-icon">
                                <span className="navbar-toggler-icon-line"/>
                            </span>
                        </NavbarToggler>
                        <Collapse isOpen={this.state.isOpen} navbar>
                            <Nav className="mx-auto main-nav" navbar>
                                <NavItem>
                                    <Link to={'/'} className="nav-link">Главная</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to={'/artists'} className="nav-link">Артисты</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to={'/songs'} className="nav-link">Медиатека</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to={'/concerts'} className="nav-link">Концерты</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to={'/shop'} className="nav-link">Магазин</Link>
                                </NavItem>
                                <NavItem>
                                    <Link to={'/contacts'} className="nav-link">Контакты</Link>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </Container>
                </Navbar>
            </header>
        );
    }
}

export default Header;


