import React, { Component } from 'react';

import { Row } from 'reactstrap';

import mainImage from '../../assets/img/101.png';

class Jumbotron extends Component {

    render() {
        return (
          <section className="main-jumbotron bg-gray bg-lined">
              <div className="container-fluid">
                  <Row className="align-items-center full-view justify-content-center py-100">
                      <img src={mainImage} alt="" style={{maxWidth: '100%'}}/>
                  </Row>
              </div>
          </section>
        );
    }

}

export default Jumbotron;


