import React, { Component } from 'react';
import { Row, Button, Table } from 'reactstrap';

class closestTours extends Component {
    constructor(props) {
        super(props);

        this.state = {};

        this.state.tours = [
            {
                day: '25',
                month: 'Июнь',
                time: '21:00',
                artist: 'NINETY ONE',
                place: 'ALMATY ARENA, Almaty, Kazakhstan',
                priceMin: '3000',
                priceMax: '15000',
                currency: 'KZT',
                item: 'Билет',
                image: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/91_logo.png?alt=media&token=423e56f7-f1f5-468a-8d2a-eb09a345b6d3'
            },
            {
                day: '12',
                month: 'Июнь',
                time: '21:00',
                artist: 'Ne NINETY ONE',
                place: 'Khalyk Arena, Almaty, Kazakhstan',
                priceMin: '3000',
                priceMax: '15000',
                currency: 'KZT',
                item: 'Билет',
                image: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/image1.png?alt=media&token=b446e43e-cc3c-42bf-8053-84040e4eff0a'
            },
            {
                day: '30',
                month: 'Август',
                time: '21:00',
                artist: 'NINETY ONE',
                place: 'MEGA, Актобе, Казахстан',
                priceMin: '3000',
                priceMax: '15000',
                currency: 'KZT',
                item: 'Билет',
                image: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/91_logo.png?alt=media&token=423e56f7-f1f5-468a-8d2a-eb09a345b6d3'
            },
            {
                day: '01',
                month: 'Сентябрь',
                time: '21:00',
                artist: 'NINETY ONE',
                place: 'ALMATY ARENA, Almaty, Kazakhstan',
                priceMin: '3000',
                priceMax: '15000',
                currency: 'KZT',
                item: 'Билет',
                image: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/image2.png?alt=media&token=0ab186ba-6d4f-4030-9d64-cc1ebd4c4e63'
            },
            {
                day: '25',
                month: 'Июнь',
                time: '21:00',
                artist: 'NINETY ONE',
                place: 'ALMATY ARENA, Almaty, Kazakhstan',
                priceMin: '3000',
                priceMax: '15000',
                currency: 'KZT',
                item: 'Билет',
                image: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/91_logo.png?alt=media&token=423e56f7-f1f5-468a-8d2a-eb09a345b6d3'
            }
        ]
    }

    getRows() {
        let rows = [];
        for (let i in this.state.tours) {
            const tour = this.state.tours[i];
            rows.push(
                <tr key={i}>
                    <td style={{lineHeight: '2rem'}}>
                        <span className="font-weight-bold" style={{fontSize: '3.4rem', lineHeight: '3.4rem'}}>
                            {tour.day}
                        </span><br/>
                        <span style={{fontSize: '1.8rem'}}>
                            {tour.month} ({tour.time})
                        </span>
                    </td>
                    <td style={{padding: '10px 20px'}}>
                        <div className="image-container mx-auto" style={{height:'50px', width:'50px'}}>
                            <img src={tour.image} alt=""  style={{maxHeight:'100%', maxWidth:'100%', height: 'auto', width: 'auto'}}/>
                        </div>
                    </td>
                    <td>
                        <h4 className="mb-0">{tour.artist}</h4>
                        <p className="mb-0">{tour.place}</p>
                    </td>
                    <td>
                        <h4 className="mb-0">
                            {tour.priceMin} - {tour.priceMax} {tour.currency}
                        </h4>
                        <p className="mb-0">
                            {tour.item}
                        </p>
                    </td>
                    <td className="text-right">
                        <Button color="danger" style={{marginTop: '7px'}}>
                            <span className="text-uppercase">Купить билет</span>
                        </Button>
                    </td>
                </tr>
            );
        }
        return rows;
    }

    render() {
        return (
            <section className={(this.props.lined ? 'bg-lined ' : '') + "bg-black color-white full-view py-100 py-md-155 d-flex align-items-center"} style={{overflowX: 'auto'}}>
                <div className="container">
                    <Row className="mb-5">
                        <div className="col-12 d-flex mb-3">
                            <h2>
                                <span className="text-uppercase">Ближайшие туры</span>
                            </h2>
                            <Button color="secondary ml-auto">
                                <span className="text-uppercase">ВСЕ КОНЦЕРТЫ</span>
                            </Button>
                        </div>
                        <div className="col-12">
                            <p>Follow us to our amazing tours in this 2018 all around the world</p>
                        </div>
                    </Row>
                    <Row>
                        <div className="col-12">
                            <Table dark className="table-tours">
                                <tbody>
                                    { this.getRows() }
                                </tbody>
                            </Table>
                        </div>
                    </Row>
                </div>
            </section>
        )
    }
}

export default closestTours;


