import React, { Component } from 'react';

import './playerPlaylist.css';

export default class PlayerPlaylist extends Component {

    getSongs() {
        let r = [];
        let songs = [
            {
                id: 1,
                title: 'Название песни №1',
                duration: '3:33',
                liked: false
            },
            {
                id: 2,
                title: 'Название песни №2',
                duration: '3:33',
                liked: false
            },
            {
                id: 3,
                title: 'Название песни №3',
                duration: '3:33',
                liked: false
            },
            {
                id: 4,
                title: 'Название песни №4',
                duration: '3:33',
                liked: true
            },
            {
                id: 5,
                title: 'Название песни №5',
                duration: '3:33',
                liked: false
            },
            {
                id: 6,
                title: 'Название песни №6',
                duration: '3:33',
                liked: false
            },
            {
                id: 7,
                title: 'Название песни №7',
                duration: '3:33',
                liked: false
            },
        ];
        for (let i in songs) {
            const song = songs[i];
            r.push(
                <li key={song.id} className="playlist-song d-flex align-items-center">
                    <div className="song-number">
                        <span className="song-number-span"> {("0" + song.id).slice(-2)} </span>
                        <span className="icon-play-full color-red song-play"/>
                    </div>
                    <div className="song-info">
                        <div>
                            <span>{song.title}</span>
                            <br/>
                            <span>{song.duration}</span>
                        </div>
                    </div>
                    <div className="song-liked ml-auto">
                        { song.liked ? <span className="icon-heart-full color-red"/> : <span className="icon-heart"/> }
                    </div>
                </li>
            )
        }

        return r;
    }

    render() {
        return (
            <ul className="player-playlist-container">
                {this.getSongs()}
            </ul>
        );
    }

}


